p, h2 {
  margin: 0;
}

body {
  background-color: #1e90ff;
}

.form {
  width: 480px;
  height: 240px;
  background-color: azure;
  outline: 1px solid #000;
  flex-direction: column;
  gap: 16px;
  padding: 24px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.input__number {
  height: 24px;
  width: 460px;
  font-size: 16px;
}

.range__input {
  background: #ecf0f1;
  border: 1px solid #0ff;
}

.label {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.amount__wrapper {
  gap: 4px;
  display: flex;
}

